.bujoEntries {

    ul {
        list-style: none;
        // border: 1px solid pink;

        li {
            display: flex;
            background: linear-gradient(to left, $colour3, $colour2 80%, $colour1 95%);
            margin: 15px 0;
            box-shadow: 5px 5px $colour4;
            // border: 3px solid $colour3;
        }

        i, p {
            padding: 10px;
        }

        i {
            width: 100px;
            color: $colour4;
        }

        p {
            width: calc(100% - 100px);
            text-align: left;
            color: $colour5;
        }
    }
}

